<template>
    <div class="sidebar">
        <div class="inner-sidebar" :class="classlistsidebar">
            <slot name="body"></slot>
        </div>
    </div>
</template>
<script>
export default {
    install: function(Vue){
        var store = new Vue({
            data: {
                status: true,
                callback: null,
            },
            methods: {
                toggle(){
                    this.setvalue(!this.status);
                },
                show(){
                    this.setvalue(true);
                },
                hide(){
                    this.setvalue(false);
                },
                setcallback(callback){
                    this.callback = callback;
                },
                setvalue(val){
                    this.status = val;
                    this.setdata(this.status);
                    if(typeof this.callback == 'function')
                        this.callback(this.status);
                },
                setname(name){
                    this.name = name;
                    this.setvalue(this.getdata(this.status));
                },
                setdata(val){
                    window.localStorage.setItem(this.name, val);
                },
                getdata(def = null){
                    let data = window.localStorage.getItem(this.name);
                    return data == null ? def : (data == 'true');
                }
            },
            created: function(){
                this.setvalue(this.getdata(this.status));
            },
        });
        Vue.prototype.$sidebar = store;
    },
    props: {
        status: {
            type: Boolean,
            default: true
        },
        classlist: {},
        name: {
            type: String,
            default: 'sidebar-default',
        }
    },
    computed: {
        classlistsidebar: function(){
            return this.classlist;
        },
    },
    methods: {
    },
    created: function(){
    }
}
</script>
<style lang="scss">
    $width: 250px;
    .wrapper{
        display: flex;
        flex-direction: row;
        &.sidebar-open{
            > .sidebar{

            }
        }
        > .sidebar{
            width: $width;
            min-width: $width;
            height: 100vh;
            position: relative;
            > .inner-sidebar{
                height: 100%;
                width: $width;
                position: fixed;
                top: 0px;
                left: 0px;
                overflow-y: auto;
            }
        }
        > .v-container{
        }
    }
    @media (max-width: 768px) {
        body{
            &.nav-open{
                overflow: hidden;
            }
        }
        body{
            overflow-x: hidden;
        }
        .wrapper {
            &.sidebar-open{
                > .sidebar{
                    transform: translateX(0%);
                }
                > .v-container{
                    transform: translateX($width);
                }
            }
            > .sidebar{
                transform: translateX(-100%);
                position: fixed;
                top: 0px;
                transition: all 0.33s cubic-bezier(0.685, 0.0473, 0.346, 1);
                > .inner-sidebar{
                    
                }
            }
            > .v-container{
                transition: all 0.33s cubic-bezier(0.685, 0.0473, 0.346, 1);
                .mx--auto{
                    margin: 0%!important;
                }
            }
        }
    }
    .rotate-enter-active{
        transition: all .6s ease .3s;
    }
    .rotate-leave-active{
        transition: all .6s ease .3s;
        position: absolute;
    }
    .rotate-enter,
    .rotate-leave-to{
        opacity: 0;
        transform: rotateZ(360deg) scale(.25);
    }
</style>